body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background-color: #f9f9f9;
  color: #333;
  display: flex;
  flex-direction: column;
  align-items: center;
}

header {
  background-color: #007BFF;
  color: white;
  padding: 20px;
  width: 100%;
  text-align: center;
}

main {
  text-align: center;
  padding: 20px;
  max-width: 600px;
  margin: auto;
}

footer {
  background-color: #333;
  color: white;
  padding: 10px;
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
}

img {
  border-radius: 10px;
  max-width: 300px;
  height: auto;
  margin-bottom: 20px;
}
